  .card {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    flex-direction: row;
    margin-bottom: 0px;
    border-radius: 10px;
    border:none;
    background-color: transparent;
  }
  .content,.card-name{
    white-space: nowrap; overflow: hidden;text-overflow: ellipsis;color:rgb(63, 65, 88);margin-bottom:0
  }
  .card-name{
    font-weight: bold;
  }
  .card-issues{
    font-size: 10;
    color:grey
  }
  .action-buttons {
    display: flex;
    gap: 10px;
  }
  .group{
    padding-left: 8px;
    color:rgba(142,142,160,1);
    padding-top:10px
  }
  .card:hover{
    background-color: #ffffff;
    text-decoration: none;
  }

  .delete-button {
    
    display: inline-flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    font-weight: 400;
    padding: 0.25rem 0.75rem;
    border-radius: 0.5rem;
    min-height: 38.4px;
    margin: 0px;
    line-height: 1.6;
    
    width: auto;
    user-select: none;
    background-color: transparent;
    border: 1px solid rgba(49, 51, 63, 0.2);
  }
  

  .delete-button:focus{
    outline: none;
  }
  
  .delete-button:active{
    color: rgb(255, 255, 255);
    border-color: rgb(255, 75, 75);
    background-color: rgb(255, 75, 75);
  }
  
  .delete-button:hover{
    border-color: rgb(255, 75, 75);
    color: rgb(255, 75, 75);
  }
  .delete-button:focus-visible{
    box-shadow: rgba(255, 75, 75, 0.5) 0px 0px 0px 0.2rem;
  }